import back from '@/components/back'

export default {
    components:{
        back
    },
    data(){
        return {
            items: [],
            act: 0,
            children: [],
            search: '',
            user: this.$store.state.userInfo.Data,
            ListDetails: this.$store.state.upList,
            medSearchList: null,
            inputFlag: false,
			medSearchList2:null,
			medBlurSH:false,
			starFlag:false,
			scrollTopDistance: 0,
        }
    },
    computed:{
        totalPrice(){
            let p = 0
            if(this.ListDetails.length){
                this.ListDetails.forEach(v => {
                    p += (+v.Price) * v.Quantity
                })
            }
            return p.toFixed(2)
        }
    },
    created(){
        this.pull()
    },
	mounted() {
		this.getMore()
		this.reload()
	},
	beforeDestroy() {
		window.onscroll = null
	},
	beforeRouteLeave(to, from, next) {
		window.onscroll = null
		if(from.meta.keepAlive) {　
			from.meta.scollTopPosition = this.scrollTopDistance;
		}
		next()
	},
	beforeRouteEnter(to, from, next) { //进入前
		next(vm => {
			let flag = false
			vm.starFlag = false
			vm.getMore()
			vm.reload()
			if((from.path != vm.$store.state.prevUrl && vm.$store.state.prevUrl != null) || !vm.$store.state.prevUrl || vm.$store.state.isFresh) {
				flag = true
			}
			if(flag) {
				vm.$store.dispatch('pushIsFresh', true)
				vm.pull(false)
			} else {
				setTimeout(() => {
					vm.$refs.el2.scrollTop = to.meta.scollTopPosition
				}, 1)
			}

		});
	},
    methods:{
		medBlur(){
			let that = this;
			setTimeout(function(){
				that.medBlurSH = false
			},100)
		},
		medInputMethods(){
			if(localStorage.getItem('searchName')){
				this.medSearchList2 = JSON.parse(localStorage.getItem('searchName')).slice(0,10)
				this.medBlurSH = true
			}
		},
		goBack(){
			if(this.$route.query.from === 'p' || this.$route.query.from === 'ump'){
				this.$router.push(`/prescription`)
			}
			else if(this.$route.query.from === 'up' || this.$route.query.from === 'upp'){
			    this.$router.push(`/usup-detail`)
			}
			else{
			    this.$router.push(`/prescription`)
			}
		},
        choose(id,idx){
            this.act = idx
            this.pull(id)
        },
		searchFor(){
			if(!localStorage.getItem('searchName')){
				var searchArr = []
			}else{
				var searchArr = JSON.parse(localStorage.getItem('searchName'))
			}
			if(searchArr.indexOf(this.search) == -1 && this.search){
				searchArr.unshift(this.search)
			}
			localStorage.setItem('searchName', JSON.stringify(searchArr))

		    this.$store.dispatch('pushsearchText',this.search)
		    if(this.$route.query.from == 'p'){
		        this.$router.push(`/medicine-list?id=&from=ump`)
		    }
		    else if(this.$route.query.from == 'up'){
		        this.$router.push(`/medicine-list?id=&from=upp`)
		    }
		    else{
		        this.$router.push(`/medicine-list?id=&from=um`)
		    }
		},
		pull(){
		    this.$post('Prescription/Medication/GetMedicationClass',{
				ParentId:0,
                DoctorId:this.user.User.DoctorId,
                OrgId:this.user.User.OrgId,
			}).then(o => {
				this.items = o
			})
		},
		starChild(i){
			this.$store.dispatch('pushIsFresh', true)
			this.$store.dispatch('pushsaveChooseMed', i)
			this.$store.dispatch('pushsaveChildMed', null)
			this.$store.dispatch('pushSortType', null)
			this.$router.push(`/chooseMedicine?from=`+this.$route.query.from)
			// this.starFlag = true
			// this.$post('Prescription/Medication/GetMedicationClass',{
			// 	ParentId:i.Id,
            //     DoctorId:this.user.User.DoctorId,
            //     OrgId:this.user.User.OrgId,
			// }).then(o => {
			// 	this.children = o
			// })
		},

//         pull(ParentId){
//             if(!ParentId){
//                 ParentId = 0
//                 this.$post('Prescription/Medication/GetMedicationClass',{
//                     ParentId,
//                     OrgId: 0,
//                     DoctorId:this.user.User.DoctorId
//                 }).then(o => {
//                     this.items = o
//                     this.pull(o[0].Id)
//                 })
//             }
//             else{
//                 this.$post('Prescription/Medication/GetMedicationClass',{
//                     ParentId,
//                     OrgId: 0,
//                     DoctorId:this.user.User.DoctorId
//                 }).then(o => {
//                     this.children = o
//                 })
//             }
//         },
        goList(id){
            id = id ? id : ''
            this.medSearchList = null
            this.$store.dispatch('pushsearchText',this.search)
            this.$router.push(`/medicine-list?id=${id}&&from=${this.$route.query.from}`)
        },
		goList1(id){
		    id = ''
		    this.medSearchList = null
		    this.$store.dispatch('pushsearchText',this.search)
		    this.$router.push(`/medicine-list?id=${id}&&from=${this.$route.query.from}`)
		},
		// starClick(){
		// 	this.medSearchList = null
		// 	this.$store.dispatch('pushsearchText',this.search)
		// 	this.$router.push(`/medicine-list?id=&&from=${this.$route.query.from}&&star=1`)
		// },
        toList(i){
			if(!localStorage.getItem('searchName')){
				var searchArr = []
			}else{
				var searchArr = JSON.parse(localStorage.getItem('searchName'))
			}
			if(searchArr.indexOf(i) == -1){
				searchArr.unshift(i)
			}
			localStorage.setItem('searchName', JSON.stringify(searchArr))
            this.$store.dispatch('pushsearchText',i)
            this.$router.push(`/medicine-list?from=${this.$route.query.from}`)
        },
        searchByName(){
            if(this.inputFlag){
                return
            }
            if(this.search){
				this.$post('Prescription/Medication/GetMedicationByNameForOftenUsed',this.$pick({
					MedicationName: this.search,
					OrgId: this.user.User.OrgId,
					DoctorId: this.user.User.DoctorId,
					PageIndex: this.page,
					PageSize: 20
				})).then(o => {
					this.medSearchList = o.ReturnData
				})
			}
        },
		getMore(){
		    const el2 = this.$refs.el2
		    let timer = null
		    el2.onscroll = () => {
		        clearTimeout(timer)
		        timer = setTimeout(() => {
					 const top = el2.scrollTop
								|| el2.pageYOffset
								|| el2.scrollTop,
		                    h = el2.scrollHeight,
		                    wh = window.innerHeight
					this.scrollTopDistance = top;

		        }, 10)
		    }
		},
		reload(){
			//ios页面卡死问题
			let el2 = this.$refs.el2
			let lastY = 0;
			el2.addEventListener('touchstart', function(event) {
				lastY = event.touches[0].clientY;
			});
			//获取滚动元素
			let scrollEle =this.$refs.el2
			//先移除监听事件
			scrollEle.removeEventListener('touchmove', function (event) {
				event.preventDefault();
			}, false);
			//再添加监听事件
			scrollEle.addEventListener('touchmove', function(event) {
				let top = event.touches[0].clientY;
				let scrollTop = scrollEle.scrollTop;
				let scrollHeight = scrollEle.scrollHeight;
				let clientHeight = scrollEle.clientHeight;
				let direction = (lastY - top) < 0 ? "up" : "down";
				if (scrollTop == 0 && direction == "up") {
				  event.preventDefault();
				} else if (scrollTop >= (scrollHeight - clientHeight) && direction == "down") {
				  event.preventDefault();
				}
				lastY = top;
			});

		},
    }
}



